
 .main{
    padding-bottom: 50px;
 }

 .container{
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1090px;
  margin: auto;
  gap: 80px;
 }
 .container .leftSide{
    flex: 1;
 }
 .container .leftSide h1{
   margin: 0px;
   font-size: 50px;
   color: #3a4b5c;
 }
 .container .leftSide p{
  font-size: 20px;
  margin-bottom: 40px;
  color: #6a7d8b;
 }
 .container .leftSide input{
  width: 100%;
  border: 0;
  border-bottom: 2px solid rgba(150, 163, 171, .5);
  padding: 10px 2px;
  margin-bottom: 20px;
  font-size: 20px;
  outline: 0;
 }
 .container .leftSide input:disabled{
   opacity: .5;
 }
 .container .leftSide button{
  background-color: #227c9d;
  color: #fff;
  font-size: 20px;
  border: 0;
  border-radius: 10px;
  padding: 15px 0;
  width: 100%;
  cursor: pointer;
  margin-top: 40px;
  transition: all ease .3s;
 }
 .container .leftSide button:hover{
   opacity: .8;
 }
 .container .leftSide button:disabled{
   opacity: .5;
 }
 .container .rghtSide{
  flex: 1;
  display: flex;
 }
 .container .rightSide .grid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}
.container .rightSide .rightBig{
  flex: 1;
  display: flex;
}
 .container .rightSide .rightArrow{
  position: absolute;
  background-color: #227c9d;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin-left: -35px;
  margin-top: 145px;
  transition: all ease .3s;
 }
 .container .rightSide .rightArrow:hover{
  opacity: .8;
 }
 @media (max-width:770px){
  .container{
    padding: 0 20px;
    flex-direction: column;
    gap: 0;
  }
  .container .rightSide{
    margin-top: 50px;
  }
  .container .rightSide .rightArrow{
    margin-left: 0;
    margin-top: 0;
    border-radius: 10px;
  }
 }
 @media (max-width:430px){
  .container .rightSide .grid{
    grid-template-columns: 1fr;
  }
 }

 
